import 'primeicons/primeicons.css';
import midbanner from '../images/mid-banner.png'

const QuemSomos = () => {
    return (
        <div className="p-quemsomos">
            <div className="p-text-quemsomos">
                <h1>Quem somos</h1>
                <p>Somos um grupo de profissionais apaixonados pelo desenvolvimento infantil, comprometidos em oferecer recursos e ferramentas para auxiliar pais e cuidadores no acompanhamento e estimulao de crianas com Transtorno do Espectro Autista (TEA). Nosso aplicativo foi desenvolvido com base em um Protocolo de Estimulao Psicomotora, apresentado no Programa de Ps-Graduao Stricto Sensu em Distrbios do Desenvolvimento, visando promover o desenvolvimento psicomotor e uma melhor qualidade de vida para as crianas com TEA.
                </p><p>Nossa misso  tornar o processo de estimulao mais acessvel, organizado e, acima de tudo, prazeroso tanto para as crianas quanto para os pais. Acreditamos que, com o ambiente adequado e atividades bem direcionadas, o potencial de cada criana pode ser estimulado de forma positiva e significativa.
                </p>

                <div className='d-flex'>
                    <div className='card-quem-somos'>
                        <img src={midbanner} />
                        <div>
                            <p>Ana Carolina Silva</p>
                            <p>Especialista em Marketing de Contedo</p>
                            <p>anacarolina@email.com</p>
                        </div>
                    </div>
                    <div className='card-quem-somos'>
                        <img src={midbanner} />
                        <div>
                            <p>Ana Carolina Silva</p>
                            <p>Especialista em Marketing de Contedo</p>
                            <p>anacarolina@email.com</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default QuemSomos;